body {
  margin: 0;
  font-family: Tahoma, Segoe UI, Geneva, Verdana, sans-serif;
}

#background-area {
  flex-direction: column;
  width: 100vw;
  max-width: 100vw;
  height: 100vh;
  max-height: 100vh;
  display: flex;
  position: relative;
  overflow: hidden;
}

#background-area:before {
  content: "";
  filter: blur(2px);
  z-index: -1;
  background-image: url("background.46cd92f5.jpeg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  inset: 0;
}

#desktop {
  flex: 1;
}

#taskbar {
  color: #fff;
  background: linear-gradient(#245edc 0%, #3f8cf3 9%, #245edc 18% 92%, #1941a5 100%) center / cover no-repeat;
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
}

#start-button {
  float: left;
  cursor: pointer;
  background: radial-gradient(circle, #5eac56 0%, #3c873c 100%) center / cover no-repeat;
  border-radius: 0 5px 5px 0;
  justify-content: center;
  align-items: center;
  gap: 10px;
  height: 100%;
  padding: 5px 25px 5px 15px;
  display: flex;
}

#start-button img {
  filter: drop-shadow(1px 1px 1px #222);
  height: 1.2vw;
}

#start-button span {
  text-shadow: 0 2px 4px #00000080;
  font-size: 1.2vw;
  font-style: italic;
  font-weight: bold;
}

#start-button:hover {
  background: radial-gradient(circle, #5eac56 0%, #3c873c 100%) center / cover no-repeat;
  box-shadow: inset 0 5px 10px #79ce71, 4px 0 8px #3f8cf3;
}
